@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  width: 100vw;
  background: #1c3e6f;
}

.navbar-brand-top {
        padding-left: 8px;
	padding-right: 8px;
        margin-top: 0;
        letter-spacing: 3px;
        color: rgba(255,255,255,.75);
        line-height: 40px;
        text-decoration: none;
}

.nav-item a {
  color: white;
  font-weight: bold;
  font-size: 20px;
  margin-right: 20px;
}

.nav-item a:hover {
  color: #c4d1e4;
}

.navbar-brand img {
  margin-left: 20px;
  max-width: 40px;
}
/* make the text go down */
.body_text {
  font-family: 'Times New Roman', Times, serif;
  position: relative;
  font-size: 150%;
  margin-left: 40px;
  margin-right: 50px;
  margin-top: 170px;
  margin-bottom: 10px;

}

*{
  font-family: 'Fredoka One', cursive;
  letter-spacing: 3px !important;
  color: #1C3E6E;
}

p {
  color: #8198B8;
}

.page-content {
  background-color: #F2F4F8;
}

.course-template {
  padding-bottom: 3%;
}

.course-template .course-title {
  padding-top: 5%;;
  padding-bottom: 3%;
}

.col-item {
  background-color: white;
  border-radius: 15px;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 2%;
  padding-bottom: 2%;
}

.course-template .syllabus {
  vertical-align: center;
  padding-top: 3%;
  padding-bottom: 3%;
}

.course-template .col-item h3 {
  margin-top: 30px;
  margin-left: 4%;
}

.course-template .col-item h4 {
  margin-top: 5%;
  margin-left: 4%;
}

.course-template .col-item p {
  margin-top: 20px;
  width: 90%;;
  margin-left: 4%;
  margin-bottom: 3%;
  color: #5C779C;
}

.course-template .course-description, .course-requisite {
  margin-left: 0;
}

.requisites-display {
  height: 120px;
  margin-top: 2%;
  background-color: #EFF3FB;
  margin-left: 3%;
  border-radius: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 4%;
}

.expand_button {
  margin-top: -5%;
}

.syllabus-link {
  margin-top: 2%;
    border-radius: 15px;
    background-color: #1C3E6E;
    color: white;
    width: 8em;
    height: 3em;
    border: none;
    font-size: 1.2em;
    text-align: center;
}

.req-graph {
  border: none;
  width: 60%;
  margin-left: 5%;
  justify-content: center;
  margin-bottom: 2%;
}
.result-display {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 3px solid #c4c4c4;
  border-radius: 20px;
  background-color: white;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  padding-top: 2%;
  padding-bottom: 2%;
  margin-top: 3%;
  margin-bottom: 3%;
}

.search-result-display {
  margin-top: 3%;
  margin-bottom: 3%;
}

.search {
  margin-top: 3%;
}

.submit-button {
  background-color: #1c3e6e;
  color: white;

  border-color: #1c3e6e;
  border-style: solid;
  border-width: 1px;
  border-radius: 15px;

  height: 3em;
  width: 7em;
}

.submit-button:hover {
  background-color: white;
  
  border-color: #1c3e6e;
  border-style: solid;
  border-width: 1px;
  border-radius: 15px;
  
  color: #1c3e6f;
}

.text-input {
  width: 40%;
  height: 3em;
  border-radius: 15px;
  border: 2px solid #c4c4c4;
  margin-right: 1%;
  padding-left: 2%;
}

.label {
    background-color: #F1F6FD;
    color: #1C3E6E;
    border-radius: 15px;
    margin-bottom: 3%;
    height: 4em;
    justify-content: center;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: row;
}
